import React from 'react'
import './dashboard.css'
import {getCookie,setCookie,delCookie} from './Cookie'
export class Dashboard extends React.Component{

  constructor(props){
    super(props);
  }

  render(){
    var uniq=localStorage.getItem("username");
    return(
    <div>
      <nav className="navbar"><label className="scale-in-ver-top" id="dash">Dashboard</label>
      <input type="button"  className="scale-in-ver-top" value='Logout' id="logout" onClick={()=>window.location.href='/'}/>

      <input type="button"  className="scale-in-ver-top" value="Change Password" id="chgpsd" onClick={()=>{var x=prompt("Enter New Password");
        if(x)
        {
          setCookie("password"+uniq,x,1);
          alert("password Changed");
        }
      }}/>
      <input type="button" value="Delete Account" id="delacc" className="scale-in-ver-top" onClick={()=>{
        var x=window.confirm("Do you really want to delete the Account");
        if(x)
        {
        delCookie("username"+uniq);
        delCookie("password"+uniq);
        delCookie("email"+uniq);
        delCookie("number"+uniq);
        localStorage.setItem("username",'');
        uniq='';
        alert("Account Deleted!");
        window.location.href='/';}
      }}/>
      </nav>
      <div className="info">
        <div className="slide-in-right">
          <h1>Welcome {getCookie("username"+uniq)}!</h1>
          <h2>Email: {getCookie("email"+uniq)}</h2>
          <h2>Number: {getCookie("number"+uniq)}</h2>
          <h2>Course: {getCookie("course"+uniq)}</h2>
        </div>
        </div>
      </div>
)
  }
}
export default Dashboard
