import React from 'react'
import './login.css'
import Dashboard from './Dashboard'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import {getCookie,setCookie} from './Cookie'
export class Login extends React.Component{

  constructor(props) {
    super(props);
    this.state = {
      username: '',
      password: ''
    };
  }

  myChangeHandler = (event) => {
    var nam = event.target.name;
    var val = event.target.value;
    this.setState({[nam]: val});
    }
  handleSubmit()
  {
    var uniq=this.state.username;
    if(getCookie("password"+uniq)==this.state.password)
    {
    localStorage.setItem("username",uniq);
    alert("Login successful");
    window.location.href='/dashboard';}
    else if(getCookie("password"+uniq)=='')
    {
      alert("First Register!");
    }
    else
    alert("Login failed");
  }

  render(){
      return(
        <div className="cover">
        <div className="scale-in-ver-top">
        <label id="log">Login</label>
        <hr/>
        <label for="username">Username</label>
        <input type="text" name="username" onChange={this.myChangeHandler}/>
        <br/>
        <label for="password">Password</label>
        <input type="password" name="password" onChange={this.myChangeHandler}/>
        <br/>
        <input type="button" id="lgn" onClick={()=>this.handleSubmit()} name="Login" value="Login" />
        <hr className="line"/>
        <label  className="links"><a href='/register' className="links">Register Here</a></label>
        </div>
        </div>
      )
  }
}
export default Login
